import React, { useState, useContext, useEffect } from "react"
import { Link, useParams } from "react-router-dom"
import firebase from "firebase"
import { Context } from "./Context"
import magnificent from "magnificent"
import Flexbox from "flexbox-react"
import ContentLoader, { Facebook } from "react-content-loader";
import RecentlyViewed from "./RecentlyViewed"
import PreloadImage from 'react-preload-image'
// import { Lines } from "react-preloaders"


function Tops() {



    const {
        wishlistItems,
        setWishlistItems,
        cartInfo,
        setCartInfo,
        addToWishlist,
        addToCart,
        removeFromWishlist,
        allProducts,
        modifyCartCount,
        modifyTotalPrice,
        modifyWishlistCount,
        itemList,
        productPageComponent,
        setProductPageComponent,
        topCategory,
        activateAddToWishlistModal,
        productOnPageCount,
        setProductOnPageCount,
        setOnCheckout,
        setCategorySearch,
        displayNumber,
        allNumber,
        itemString,
        setTermsDisplay,
        activateRemoveFromWishlistModal,
        setOnProductPage,
        isLargeScreen
    } = useContext(Context)


    useEffect(() => {
        setProductOnPageCount(0)
        setOnCheckout(false)
        setOnProductPage(false)
        document.getElementById("firebaseui-auth-container").style.display = "none"
        setCategorySearch("top")
        // document.getElementById('smallSearch').style.display = "inline-block"
        // document.getElementById('largeSearch').style.display = "initial"
    }, [])



    function handleProductComponent(item) {
        console.log(productPageComponent)
        setProductPageComponent(item);
        console.log(productPageComponent)
    }


    const [tops, setTops] = useState([])

    useEffect(() => {

        const topNames = topCategory.map(item => {
            return item[0]
        })
        setTops(topNames)
    }, [topCategory])


    /////////////////////////////////////////////////////////////////
    // <   AutoComplete Code Section >
    /////////////////////////////////////////////////////////

    function autocomplete(inp, arr) {
        /*the autocomplete function takes two arguments,
        the text field element and an array of possible autocompleted values:*/
        var currentFocus;
        /*execute a function when someone writes in the text field:*/
        inp.addEventListener("input", function (e) {
            var a, b, i, val = this.value;
            /*close any already open lists of autocompleted values*/
            closeAllLists();
            if (!val) { return false; }
            currentFocus = -1;
            /*create a DIV element that will contain the items (values):*/
            a = document.createElement("DIV");
            a.setAttribute("id", this.id + "autocomplete-list");
            a.setAttribute("class", "autocomplete-items");
            /*append the DIV element as a child of the autocomplete container:*/
            this.parentNode.appendChild(a);
            /*for each item in the array...*/
            for (i = 0; i < arr.length; i++) {
                /*check if the item starts with the same letters as the text field value:*/
                if (arr[i].substr(0, val.length).toUpperCase() == val.toUpperCase()) {
                    /*create a DIV element for each matching element:*/
                    b = document.createElement("DIV");
                    /*make the matching letters bold:*/
                    b.innerHTML = "<strong>" + arr[i].substr(0, val.length) + "</strong>";
                    b.innerHTML += arr[i].substr(val.length);
                    /*insert a input field that will hold the current array item's value:*/
                    b.innerHTML += "<input type='hidden' value='" + arr[i] + "'>";
                    /*execute a function when someone clicks on the item value (DIV element):*/
                    b.addEventListener("click", function (e) {
                        /*insert the value for the autocomplete text field:*/
                        inp.value = this.getElementsByTagName("input")[0].value;
                        /*close the list of autocompleted values,
                        (or any other open lists of autocompleted values:*/
                        closeAllLists();
                    });
                    a.appendChild(b);
                }
            }
        });
        /*execute a function presses a key on the keyboard:*/
        inp.addEventListener("keydown", function (e) {
            var x = document.getElementById(this.id + "autocomplete-list");
            if (x) x = x.getElementsByTagName("div");
            if (e.keyCode == 40) {
                /*If the arrow DOWN key is pressed,
                increase the currentFocus variable:*/
                currentFocus++;
                /*and and make the current item more visible:*/
                addActive(x);
            } else if (e.keyCode == 38) { //up
                /*If the arrow UP key is pressed,
                decrease the currentFocus variable:*/
                currentFocus--;
                /*and and make the current item more visible:*/
                addActive(x);
            } else if (e.keyCode == 13) {
                /*If the ENTER key is pressed, prevent the form from being submitted,*/
                e.preventDefault();
                if (currentFocus > -1) {
                    /*and simulate a click on the "active" item:*/
                    if (x) x[currentFocus].click();
                }
            }
        });
        function addActive(x) {
            /*a function to classify an item as "active":*/
            if (!x) return false;
            /*start by removing the "active" class on all items:*/
            removeActive(x);
            if (currentFocus >= x.length) currentFocus = 0;
            if (currentFocus < 0) currentFocus = (x.length - 1);
            /*add class "autocomplete-active":*/
            x[currentFocus].classList.add("autocomplete-active");
        }
        function removeActive(x) {
            /*a function to remove the "active" class from all autocomplete items:*/
            for (var i = 0; i < x.length; i++) {
                x[i].classList.remove("autocomplete-active");
            }
        }
        function closeAllLists(elmnt) {
            /*close all autocomplete lists in the document,
            except the one passed as an argument:*/
            var x = document.getElementsByClassName("autocomplete-items");
            for (var i = 0; i < x.length; i++) {
                if (elmnt != x[i] && elmnt != inp) {
                    x[i].parentNode.removeChild(x[i]);
                }
            }
        }
        /*execute a function when someone clicks in the document:*/
        document.addEventListener("click", function (e) {
            closeAllLists(e.target);
        });
    }


    /////////////////////////////////////////////////////////////////
    // </  AutoComplete Code Section >
    /////////////////////////////////////////////////////////

    let emptyHeartSource = "https://firebasestorage.googleapis.com/v0/b/jiva-website-405ed.appspot.com/o/heart%20transparent.svg?alt=media&token=812d1dc3-3a82-4527-8551-63d3941fc5c4"
    let filledHeartSource = "https://firebasestorage.googleapis.com/v0/b/jiva-website-405ed.appspot.com/o/heart%20pinkers.svg?alt=media&token=48f11612-1d2f-4564-8625-4c0821c9df4c"

    function handleHideHeart(itemName) {
        let heartIcon = document.getElementById(`heartIcon${itemName}`)

        if (heartIcon.src == emptyHeartSource) {
            heartIcon.style.display = "none"
            heartIcon.setAttribute("display", "none")
        }
    }

    function handleShowHeart(itemName) {
        let heartIcon = document.getElementById(`heartIcon${itemName}`)

        if (heartIcon.src == emptyHeartSource) {
            heartIcon.style.display = "initial"
            heartIcon.setAttribute("display", "initial")
        }

    }

    function handleWishlist(count, item) {
        let putBackItem = [item[0], [], (Number(item[2]) / 100), [], [], `${item[1]}`]

        if (count > 0) {
            removeFromWishlist(item[0])
            activateRemoveFromWishlistModal(putBackItem)
            modifyWishlistCount(-1)
        } else {
            addToWishlist(item)
            activateAddToWishlistModal(item[0])
            modifyWishlistCount(1);
        }
    }


    useEffect(() => {
        setTermsDisplay(false);
        document.getElementById("firebaseui-auth-container").style.display = "none"
    }, [])

    useEffect(() => {
        window.scroll(0, 100)
    }, [])


    return (
        <div>
            <nav className="darkNav" aria-label="You are here:" role="navigation">
                <ul class="breadcrumbs">
                    <li><Link to="/">Home</Link></li>
                    <li><Link to="/shop">Shop</Link></li>
                    <li><Link to="/shop/tops">Tops</Link></li>
                    <li>
                        <span class="show-for-sr">Current: Tops</span>
                    </li>
                </ul>
            </nav>
            <div className="shadowed" style={{
                // "background-color": "black",
                // "box-shadow": "0px 0px 5px white",
            }}>
                <h2>Tops</h2>
                <br></br>
                {displayNumber &&
                    <p
                        style={{
                            "background-color": "#565759",
                            "padding": ".25em",
                            "border-radius": "13px",
                            "width": `${isLargeScreen ? "30%" : "90%"}`,
                            "zIndex": "86",
                            "left": `${isLargeScreen ? "35vw" : "5vw"}`,
                            "position": "relative",
                            "whiteSpace": "nowrap"
                        }}>{(allNumber > 0) && allNumber} {itemString}</p>}
                {topCategory.map(item => {
                    let refString = item[1].toString()
                    console.log(item)
                    console.log(item[0])
                    console.log(refString)

                    let productInWishlistArray = wishlistItems.filter(i => i.includes(item[0]))
                    let productInCartArray = itemList.filter(i => i.includes(item[0]))


                    let productPageLinkString = item[0].split(" ").join("-")

                    let numberInCart = itemList.filter(i => i[0] == item[0])
                    let numInCart = numberInCart.length
                    let bestseller = item[11]

                    let posArray = []
                    let rightArray = []
                    let emptyString = ""
                    let sale = item[12]

                    let moddedWidth = `${posArray[1]} + 15px`

                    return (
                        <div className="column shadowed">
                            <div className="productCard">
                                <div class="img-responsive">
                                    <div id={`contentLoader${tops.indexOf(item[0])}`}>
                                        <ContentLoader
                                            backgroundColor="white"
                                            animate={false}
                                            style={{
                                                // "background-Color": "#565759",

                                                "borderRadius": "25px",
                                                "height": "500px"
                                            }}
                                            onLoad={() => {
                                                document.getElementById(`spinner${item[0]}`).style.visibility = "visible"
                                                document.getElementById(`spinner${item[0]}`).setAttribute("visibility", "visible")
                                            }
                                            }
                                        >
                                            <rect style={{
                                                "background-Color": "white",
                                                "zIndex": "85",
                                                "borderRadius": "25px",
                                            }} x="0" y="0" rx="5" ry="5" width="400" height="500" />

                                            <rect x="80" y="17" rx="4" ry="4" width="300" height="13" />
                                            <rect x="80" y="40" rx="3" ry="3" width="250" height="10" />
                                        </ContentLoader>
                                        <div id={`spinner${item[0]}`}
                                            style={{
                                                "position": "relative",
                                                "top": "-300px", "zIndex": "86", "visibility": "none"
                                            }}
                                            className="loader centered" id={`spinner${tops.indexOf(item[0])}`}>


                                        </div>
                                    </div>
                                    <div className="tops"
                                        onMouseOver={() => { handleShowHeart(`${item[0]}`) }}
                                        onMouseOut={() => { handleHideHeart(`${item[0]}`) }}
                                        id={`tops${item[0].toUpperCase()}`}>
                                        {bestseller && <span class="label warning" style={{
                                            "color": "white",
                                            "top": `${(posArray[1]) ? posArray[0] : emptyString}`,
                                            "left": `${(posArray[1]) ? posArray[1] : emptyString}`,
                                            "position": "absolute"
                                        }}>Bestseller</span>}
                                        <img
                                            onClick={() => {
                                                handleWishlist(productInWishlistArray.length, item);
                                            }}
                                            id={`heartIcon${item[0]}`}
                                            style={{
                                                "top": `${(posArray[1]) ? posArray[0] : emptyString}`,
                                                "margin-left": "16em",
                                                // "left": `${(rightArray[0]) ? rightArray[0] : emptyString}`,
                                                // "left": "4em",
                                                "display": `${(productInWishlistArray.length > 0) ? "initial" : "none"}`,
                                                "position": "absolute"
                                            }}
                                            width={`${isLargeScreen ? "3%" : "3em"}`}
                                            height="auto"
                                            src={`${(productInWishlistArray.length > 0) ? filledHeartSource : emptyHeartSource}`} />
                                        <Link to={`/product/${productPageLinkString}`}>
                                            <img

                                                style={{ "visibility": "hidden" }}
                                                id={`${item[0]}`}
                                                width="100%"
                                                height="auto"
                                                onLoad={() => {
                                                    posArray.push(document.getElementById(`${item[0]}`).getBoundingClientRect().top + window.scrollY);
                                                    posArray.push(document.getElementById(`${item[0]}`).getBoundingClientRect().left);
                                                    rightArray.push(
                                                        (document.getElementById(`${item[0]}`).getBoundingClientRect().left)
                                                        + document.getElementById(`${item[0]}`).getBoundingClientRect().width);
                                                    console.log(document.getElementById(`${item[0]}`).getBoundingClientRect());
                                                    document
                                                        .getElementById(`contentLoader${tops.indexOf(item[0])}`)
                                                        .style.display = "none"

                                                    document
                                                        .getElementById(`contentLoader${tops.indexOf(item[0])}`)
                                                        .setAttribute("display", "none")
                                                    document.getElementById(`${item[0]}`).style.visibility = "visible"

                                                }}
                                                src={item[5]}></img>
                                        </Link>
                                        <Link to={`/product/${productPageLinkString}`}>
                                            <h2 class="text-decoration-none">{item[0]}</h2>
                                        </Link>
                                        <div className="row">
                                            <Flexbox flexDirection="row">
                                                {!sale ?
                                                    <p className="column">${item[2]}.00</p>
                                                    :
                                                    <Flexbox flexDirection="row"
                                                        style={{ "position": "relative" }}>
                                                        <p
                                                            style={{
                                                                "margin-right": "2vw",
                                                                "color": "#565759",
                                                                "opacity": "0.9"
                                                            }}>
                                                            <del>${item[2]}.00</del>
                                                        </p>
                                                        <p
                                                            style={{
                                                                "margin-right": "2vw",
                                                                "left": "2vw",
                                                                "position": "relative"

                                                            }}
                                                        >SALE: ${item[12]}.00</p>
                                                    </Flexbox>
                                                }
                                                {(numInCart >= 1) &&
                                                    <p style={{
                                                        "background-color": "#565759",
                                                        // "padding-left": ".25em",
                                                        // "padding-right": ".25em",
                                                        // "padding-top": ".25em",
                                                        "border-radius": "10px",
                                                        // "bottom": "1em",
                                                        "position": "relative",
                                                        "left": "2vw",
                                                        "marginLeft": ".5em",
                                                        "marginRight": `${isLargeScreen ? ".5em" : "1em"}`,
                                                        "whiteSpace": "nowrap",
                                                        // "margin-left": "1em"
                                                    }}>{numInCart} in cart</p>}
                                            </Flexbox>
                                        </div>
                                        <p style={{
                                            "background-color": "#565759",
                                            "border-radius": "10px",
                                        }}>{`${item[3]}`}</p>
                                        {/* <p><button
                                            type="button"
                                            style={{ "backgroundColor": "#1779ba" }}
                                            onClick={() => {
                                                addToWishlist(item);
                                                modifyWishlistCount(1);
                                                activateAddToWishlistModal(item[0]);
                                            }
                                            }>{(productInWishlistArray.length > 0) ? "✔ In Wishlist" : "Add to Wishlist"}</button></p> */}

                                    </div>
                                </div>
                            </div>
                        </div >
                    )
                })}
                <br></br>
            </div>
            {/* <RecentlyViewed />
            <br></br> */}
            {/* <nav className="darkNav" style={{}} aria-label="You are here:" role="navigation">
                <ul class="breadcrumbs">
                    <li><Link to="/">Home</Link></li>
                    <li><Link to="/shop">Shop</Link></li>
                    <li><Link to="/shop/tops">Tops</Link></li>
                    <li>
                        <span class="show-for-sr">Current: Tops</span>
                    </li>
                </ul>
            </nav> */}
        </div>
    )
}

export default Tops